import React, { FC } from "react";

import Container from "@mui/material/Container";
import Divider from "@mui/material/Divider";
import Chip from "@mui/material/Chip";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Accordion from "@mui/material/Accordion";
import SchoolIcon from "@mui/icons-material/School";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import ComputerIcon from "@mui/icons-material/Computer";
import MailIcon from "@mui/icons-material/Mail";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { graphql } from "gatsby";
import Stack from "@mui/material/Stack";
import Layout from "../components/layout";
import { BranchProps } from "../pages/branches";

export type collegeData = {
  con: string;
  coc: string;
  brc: string;
  brn: string;
  id: string;
  OC: number;
  BC: number;
  BCM: number;
  MBCV: number;
  MBCDNC: number;
  MBC: number;
  SC: number;
  SCA: number;
  ST: number;
};

export type CollegeDetailsProps = {
  name: string;
  code: string;
  city: string;
  address: string;
  email: string;
  id: string;
  isWomensCollege: boolean;
  latitude: number;
  longitude: number;
  website: string;
  phone: {
    phone: string;
  }[];
  college_type: {
    name: string;
    id: string;
  };
  branches: BranchProps[];
};

type DataProps = {
  data: {
    allCutoff2021Json: {
      nodes: collegeData[];
    };
    allRanks2021Json: {
      nodes: collegeData[];
    };
  };
  pageContext: {
    name: string;
    collegeData: CollegeDetailsProps;
    allBranches: BranchProps[];
  };
};

type CutoffViewProps = {
  data: collegeData[];
  allBranches: BranchProps[];
};

const CutoffView: FC<CutoffViewProps> = ({ data, allBranches }) => (
  <>
    {data.map((item) => {
      const branchName = allBranches.find((b) => b.code === item.brc)?.name;
      return (
        <div key={item.id}>
          <Typography variant="body1" gutterBottom sx={{ marginTop: 1 }}>
            {branchName || item.brn}
          </Typography>
          <Box
            sx={{
              overflow: "scroll",
              paddingTop: 1,
              paddingBottom: 1,
            }}
          >
            <Stack
              direction="row"
              divider={<Divider orientation="vertical" flexItem />}
              spacing={2}
            >
              {item.OC > 0 && <Chip label={`OC: ${item.OC}`} />}
              {item.BC > 0 && <Chip label={`BC: ${item.BC}`} />}
              {item.BCM > 0 && <Chip label={`BCM: ${item.BCM}`} />}
              {item.MBCV > 0 && <Chip label={`MBCV: ${item.MBCV}`} />}
              {item.MBCDNC > 0 && <Chip label={`MBCDNC: ${item.MBCDNC}`} />}
              {item.MBC > 0 && <Chip label={`MBC: ${item.MBC}`} />}
              {item.SC > 0 && <Chip label={`SC: ${item.SC}`} />}
              {item.SCA > 0 && <Chip label={`SCA: ${item.SCA}`} />}
              {item.ST > 0 && <Chip label={`ST: ${item.ST}`} />}
            </Stack>
          </Box>
        </div>
      );
    })}
  </>
);

const CollegeDetails: React.FC<DataProps> = ({ data, pageContext }) => {
  //   console.log(data);
  // console.log(collegeData);
  const {
    allCutoff2021Json,
    allRanks2021Json,
  } = data;
  const { collegeData, allBranches } = pageContext;
  // console.log(collegeData);
  return (
    <Layout headerTitle={collegeData.name} shouldGoBack>
      <Container sx={{ my: 4 }}>
        <Box
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
        >
          <Typography variant="h5" gutterBottom component="div">
            {collegeData.name} - {collegeData.code}
          </Typography>
        </Box>
        {!!collegeData?.college_type?.name && (
          <Chip
            label={collegeData.college_type.name}
            size="small"
            sx={{ mb: 2 }}
          />
        )}
        <Typography variant="body1" gutterBottom component="div">
          {collegeData.address}
        </Typography>
      </Container>

      <Divider />
      <Container sx={{ my: 4 }}>
        <Stack direction="row" spacing={2} sx={{ mb: 1 }}>
          <LocalPhoneIcon color="secondary" />
          <Typography variant="body1" component="div">
            {collegeData.phone.map((item) => item.phone).join(", ") || "--"}
          </Typography>
        </Stack>
        <Stack direction="row" spacing={2} sx={{ mb: 1 }}>
          <MailIcon color="secondary" />
          <Typography variant="body1" component="div">
            {collegeData.email || "--"}
          </Typography>
        </Stack>
        <Stack direction="row" spacing={2} sx={{ mb: 1 }}>
          <ComputerIcon color="secondary" />
          <Typography variant="body1" component="div">
            {collegeData.website || "--"}
          </Typography>
        </Stack>
      </Container>

      <Divider />

      <Container sx={{ my: 4 }}>
        <Grid
          container
          direction="row"
          // justifyContent="center"
          alignItems="center">
          <Stack direction="row" spacing={2} sx={{ mb: 1 }}>
            <SchoolIcon color="secondary" />
            <Typography variant="body1" component="div">
              Branches
            </Typography>
          </Stack>
        </Grid>
        
        {allCutoff2021Json.nodes.map((item, index) => {

      const branchName = allBranches.find((b) => b.code === item.brc)?.name;
          return (
            <Typography variant="body1" component="div" key={item.id}
            gutterBottom>
            {index + 1}. {branchName || item.brn}
            </Typography>
          )})}
      </Container>

      <Accordion TransitionProps={{ unmountOnExit: true }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>Cutoff 2021</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <CutoffView
            data={allCutoff2021Json.nodes}
            allBranches={allBranches}
          />
        </AccordionDetails>
      </Accordion>
      
      <Accordion TransitionProps={{ unmountOnExit: true }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography>Ranks 2021</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <CutoffView data={allRanks2021Json.nodes} allBranches={allBranches} />
        </AccordionDetails>
      </Accordion>
      
      <Container sx={{ height: 100 }} />
    </Layout>
  );
};

export const query = graphql`
  query ($collegeCode: Int!) {
    allCutoff2021Json(filter: { coc: { eq: $collegeCode } }) {
      nodes {
        con
        coc
        brc
        brn
        id
        OC
        BC
        BCM
        MBCV
        MBCDNC
        MBC
        SC
        SCA
        ST
      }
    }
    allRanks2021Json(filter: { coc: { eq: $collegeCode } }) {
      nodes {
        con
        coc
        brc
        brn
        id
        OC
        BC
        BCM
        MBCV
        MBCDNC
        MBC
        SC
        SCA
        ST        
      }
    }
  }
`;

export default CollegeDetails;
